












export default {
	data() {
		return {
			id:0,
			info:''
		};
	},
	onLoad(option) {
		var that = this;
		console.log(option.id)
		that.id = option.id;
		uni.showLoading({
			title:'加载中...'
		})
		uni.request({
			url:that.http+'index/getNetList',
			header:that.header,
			method:'POST',
			data:{
				area_id:that.id
			},
			success: (res) => {
				uni.hideLoading()
				console.log(res)
				if(res.data.code!=200){
					uni.showToast({
						title:res.data.mes,
						icon:'none'
					})
				}else{
					that.info = res.data.data
				}
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	}
}
